@import url(https://fonts.googleapis.com/css2?family=EB+Garamond&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: burlywood;
  background-image: url(/Kapitolos/unitedstatesbrewerybrowserser.git/static/media/bg.b43a666b.jpg);
  background-size: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: 'EB Garamond', serif;
}

h4 {
  font-family: 'EB Garamond', serif;
  font-style: italic;
}

::-webkit-scrollbar {
	width: 10px;
  }
  
  ::-webkit-scrollbar-track {
	box-shadow: inset 0 0 2px rgb(2, 2, 0);
  }
  
  ::-webkit-scrollbar-thumb {
	background: rgb(255, 173, 49);
	border-radius: 35px;
  }

  a {
    text-decoration: none;
    color:black;
  }
.App {
  text-align: center;
}



#brewlist {
    text-align: center;
    position: absolute;
    margin-top: 1%;
    margin-left: 30%;
    border-style: solid;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 40%;
    height: 61%;
	background:linear-gradient(rgb(255, 255, 161), white);
	padding: 10px;
}

#brewtext {
    -webkit-animation-name: appear;
            animation-name: appear;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }
  @-webkit-keyframes appear{
    from{opacity: 0;}
    to{opacity: 1;}
  }
  @keyframes appear{
    from{opacity: 0;}
    to{opacity: 1;}
  }
#brewselect {
color: rgb(92, 90, 0);
margin-left: 35%;
margin-top: 1%;
background:linear-gradient(rgb(255, 255, 161), white);
padding: 10px;
text-align: center;
border-style: solid;
width: 30%;
}

#subbut{
	border-radius: 15%;
	background-color: rgb(252, 252, 220) ;
}
